@import url(https://fonts.googleapis.com/css2?family=Kalam:wght@300;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  scroll-behavior: smooth;
}

body {
    /* COLOR INFO */
    /* https://www.colorhexa.com/2d4466 */

    /* background-color: rgb(45,102,79); */
    /* background-color: rgb(103, 76, 45); */
    /* background-color: rgb(102,45,68); */

  background: linear-gradient(90deg, rgb(251, 254, 255) 0%,white 50%, rgb(251, 254, 255) 95%, rgb(251, 254, 255) 100%);

  background: linear-gradient(90deg, var(--bg-color) 0%,white 50%, var(--bg-color) 95%, var(--bg-color) 100%);
  font-family: 'Open Sans', sans-serif;
  font-family: var(--body-font);
}

.sectionHeader {
  color: rgb(45,68,102);
  color: var(--header-font-color);
  font-family: 'Kalam', cursive;
  font-family: var(--header-font);
  text-align: center;
  font-size: 2em;
}

.sectionHr {
  width: 60%;
  margin: 20px auto 30px auto;
  border-color: rgb(45,68,102);
  border-color: var(--header-font-color);
  border-width: medium;
}

:root {
  --header-font-color: rgb(45,68,102);
  --bg-color: rgb(251, 254, 255);
  --header-font: 'Kalam', cursive;
  --body-font: 'Open Sans', sans-serif;
}
/* MOBILE VIEW */
@media only screen and (max-width: 600px) {
  .header {
    background: rgb(2,0,36);
    background: linear-gradient(rgb(45,68,102) 10%, white);     
}

.titleText {
    color: white;
    text-align: center;
    padding-top: 50%;
}

  .navContainer {
      color: white;
  }
}

/* DESKTOP VIEW */
@media only screen and (min-width: 601px) {
    .header {
        background: url("/portfolio/static/media/Haystack Mtn.53627ae1.jpg") no-repeat center fixed;
        
        box-shadow: 0 10px 15px rgba(48, 48, 48, 0.555);
    }

    .headerLayer {
        width: 100%;
        height: 100%;
        background-color:rgba(0,0,0,0.25);
        z-index: 2;
    }

    .titleText {
        position: absolute;
        text-align: right;
        top: 125px;
        right: 160px;
        font-size: x-large;
        z-index: 3;
        color: white;
    }
    
    .titleText h1, h3 {
        font-weight: 400;
        letter-spacing: 0.1em;
    }
}

.header {
    background-size: cover;
    margin: 0;
    text-shadow: 2px 2px rgb(0,0,0,.30); 
    height: 100vh;
    font-family: var(--header-font);
}

#link {
    color: white;
    font-size: 1.5em;
    margin-left: 16px;
}

#link:hover {
    color: rgb(148, 148, 148);
}
.contact {
    height: 100vh;
    padding-top: 60px;
    margin-bottom: 50px;
}

.contactForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.contactStack {
   display: flex;
   justify-content: space-between;
   width: 70%;
   flex-flow: row wrap;
   flex-wrap: wrap;
   margin: 0px;
}

/* Phone view */
@media only screen and (max-width: 630px) {
    #nameInput {
        width: 400px;
    }

    #emailInput {
        width: 400px;
    }

    .contact {
        margin-bottom: 0;
    }
}

/* Desktop view */
@media only screen and (min-width: 600) {
    #nameInput {
        width: 300px;
    }

    #emailInput {
        width: 300px;
    }
}

.contactMessage {
    width: 70%;
}

#sendButton {
    margin-top: 15px;
    border: thin solid var(--header-font-color);
    border-radius: 15px;
    background-color: var(--header-font-color);
    color: white;
}

#sendButton:hover {
    background-color: transparent;
    color: var(--header-font-color);
}

#contactHr {
    margin-bottom: 20px;
}

.contactLinkContainer {
    width: 100%;
    text-align: center;
    margin-bottom: 5px;
}

.contactLink {
    font-size: 2em;
    color: var(--header-font-color);
    margin: 0 15px 0 15px;
}

.contactLink:hover {
    color: black;
}
.aboutTotal {
    padding-top: 60px;
}

.about {
    margin: 25px;
}

.aboutText {
    margin: 30px 50px auto 50px;
    text-align: right;
    margin-bottom: 50px;
}

.aboutLink {
    /* color: var(--header-font-color); */
}

@media screen and (max-width: 600px) {
    .about {
        margin: 0;
    }
    .aboutText {
        text-align: center;
    }
}
.skills {
    margin: 20px;
    margin-bottom: 50px;
    text-align: center;
    width: 100%;
}

.skills ul {
    display: flex;
    justify-content: center;
    flex-flow: row wrap;
    padding-top: 15px;
}

.skills li {
    list-style-type: none;
    margin: 0 50px 0 0;
}

@media screen and (max-width: 600px) {
    .skills {
        width: 75%;
        align-items: center;
    }
    .skills li {
        margin: inherit;
        padding: 0 5px 0 5px;
    }
}
.myNavbar {
    position: fixed;  
    width: 100%;
    z-index: 5;
}

/* push navlinks to the right on mobile */
.navbar-collapse {
    text-align: right;
}

.nav-link, .navbar-brand {
    color: inherit !important;
}

a.nav-link:hover {
    color: white !important;
    background-color: rgba(39, 39, 39, 0.432);
}

a.navbar-brand:hover {
    color: black !important;
}
.footer {
    height: 10vh;
    background-color: var(--header-font-color);
    color: white;
    text-align: center;
    padding: 10px;
    font-size: small;
}
.projects {
    padding-top: 60px;
}

.projectCard {
    width: 80vw;
    max-width: 1000px;
    height: 200px;
    margin: 0 auto 50px auto;
    border-radius: 15px;
    display: flex;
    flex-direction: row;
    overflow: hidden;
}

.projectTitle {
    margin-bottom: 0;
    margin-top: 0;
    color: var(--header-font-color);
    text-align: center !important;
    font-weight: 500;
    letter-spacing: 0;
}

.right {
    float: right;
}

.left {
    float: left;
}

.projectDesc {
    width: 40%;
    height: 100%;
    padding: 10px;
    padding-top: 15px;
    text-align: center;
}

.projectMedia {
    max-width: 50%;
    height: 100%;
    position: relative;
    overflow: hidden;
}

.projectCard img {
    max-width: 100%;
    height: 100%;
    border-radius: 15px;
}

.projectCardButtons {
    width: 10%;
}

#projectLink {
    color: var(--header-font-color);
    margin-right: 10px;
}

#projectLink:hover{
    color: black;
}


/* Img hover fx */
.projectPicLayer {
    position: absolute;
    z-index: 2;
    width: 100%;
    height: 0%;
    border-radius: 15px;
    background-color: rgba(45, 68, 102, 0.589);
    transition: height 0.5s; 
    overflow: hidden;
}

.projectPicLayer p {
    color: white;
    text-align: center;
    background-color: rgba(45, 68, 102, 0.726);
    position: absolute;
    bottom: 45%;
    width: 100%;
    /* opacity: 0%; */
    /* transition: opacity 1s; */
}

.projectMedia:hover .projectPicLayer {
    height: 100%;
}

.projectMedia:hover .projectPicLayer p {
    /* opacity: 1; */
}


/* Phone view */
@media only screen and (max-width: 885px) {
    
    .projectCardContainer {
        display: flex;
        flex-flow: row wrap;
    }
    
    .projectMedia {
        display: none;
        width: 0%;
        height: 0%;
    }

    .projectCard {
        height: 100%;
        padding: 5px;
        justify-content: center;
        margin-bottom: 30px;
        background-color: rgb(211, 211, 221);
    }
    
    .projectDesc {
        width: inherit;
        height: 100%;
        float: none;
        display: flex;
        flex-flow: column wrap;
        justify-content: center;
        align-items: center;
        text-align: center;
    }
}
